import { Component, OnInit } from '@angular/core';
import { OrderHttpService } from './../../../http/order.http.service';
import { PayToolBean } from './bean/pay-tool-manage.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
//支付工具管理
@Component({
  selector: 'app-pay-tool-manage',
  templateUrl: './pay-tool-manage.component.html',
  styleUrls: ['./pay-tool-manage.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class PayToolManageComponent implements OnInit {
  //表数据
  tableData: any = [];
  // bean
  payToolBean: PayToolBean = new PayToolBean();
  //模态框的标题
  headerTitle: string = "修改";
  //模态框是否显示
  display: boolean = false;
  showModel: string;
  //修改的参数
  updateValue: any = [];
  //分页
  first: number = 0;
  total: number; //总页数
  //提示信息
  msgs: Message[] = [];

  constructor(
    public orderHttpService: OrderHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
    this.payToolBean.pageParam.pageSize = 10;
    this.payToolBean.pageParam.pageNumber = 1;
  }

  ngOnInit() {
    //调查询
    this.query();
    //按回车键查询
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.queryClick();
      }
    }
  }

  //查询点击事件
  queryClick() {
    this.first = 0;
    this.payToolBean.pageParam.pageNumber = 1;
    this.query();
  }

  //查询
  query() {
    this.orderHttpService.searchPaymentTool(this.payToolBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.paymentToolList;
        this.total = data.totalCounts;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '查询提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '查询提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '查询提示', detail: '调用服务失败！' });
    })
  }

  //新增按钮事件
  addClick() {
    this.headerTitle = '新增';
    this.display = true;
    this.showModel = '1';
  }

  //添加的回调
  addCall(param) {
    this.display = param;
    this.query();
  }

  //修改按钮事件
  modifyClick(item) {
    this.headerTitle = '修改';
    this.display = true;
    this.showModel = '2';
    this.updateValue = item.paymentMethodTypeId;
  }

  //修改的回调
  updateCall(param) {
    this.display = param;
    this.query();
  }

  //删除按钮事件
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          paymentMethodTypeId: item.paymentMethodTypeId
        };
        this.orderHttpService.deletePaymentTool(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.queryClick();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '删除提示', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '删除提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '删除提示', detail: "调用服务失败！" });
        })
      }
    });
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.payToolBean.pageParam.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.payToolBean.pageParam.pageNumber = currentPage;
    this.first = event.page * event.rows;
    //调用查询的方法
    this.query();
  }

  //重置
  resetClick() {
    this.payToolBean = new PayToolBean();
    this.payToolBean.pageParam.pageSize = 10;
    this.payToolBean.pageParam.pageNumber = 1;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}

