import { Component, OnInit } from '@angular/core';
import { OrderHttpService } from './../../../http/order.http.service';
import { PayChannelBean } from './bean/pay-channel-manage.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { specialSymbol} from 'app/@uisftech/common/service/validator';
//支付通道管理
@Component({
  selector: 'app-pay-channel-manage',
  templateUrl: './pay-channel-manage.component.html',
  styleUrls: ['./pay-channel-manage.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class PayChannelManageComponent implements OnInit {
  //表数据
  tableData: any = [];
  // bean
  payChannelBean: PayChannelBean = new PayChannelBean();
  //模态框的标题
  headerTitle: string = "修改";
  //模态框是否显示
  display: boolean = false;
  showModel: string;
  //修改的参数
  updateValue: any = [];
  //分页
  total: string; //总页数
  first: number = 0;//控制页码
  //提示信息
  msgs: Message[] = [];
  //校验
  userform: FormGroup;

  constructor(
    public orderHttpService: OrderHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
    this.payChannelBean.pageSize = 10;
    this.payChannelBean.pageNum = 1;
  }


  ngOnInit() {
    //表单校验
    this.userform = this.fb.group({
      'paymentGatewayConfigId': new FormControl('', Validators.compose([specialSymbol])),
    });
    //调服务
    this.query();
    //按回车键查询
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.queryClick();
      }
    }
  }

  //查询点击事件
  queryClick() {
    this.payChannelBean.pageNum = 1;
    this.first = 0;
    this.query();
  }

  //查询
  query() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    this.orderHttpService.findByConPaymentGatewayConfig(this.payChannelBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.returnList;
        this.total = data.totalCounts;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '查询提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '查询提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '查询提示', detail: '调用服务失败！' });
    })
  }

  //新增按钮事件
  addClick() {
    this.headerTitle = '新增';
    this.display = true;
    this.showModel = '1';
  }
  //添加的回调
  addCall(param) {
    this.display = param;
    this.query();
  }


  //修改按钮事件
  modifyClick(item) {
    this.headerTitle = '修改';
    this.display = true;
    this.showModel = '2';
    this.updateValue = item.paymentGatewayConfigId;
  }
  //修改的回调
  updateCall(param) {
    this.display = param;
    this.query();
  }

  //删除按钮事件
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = { paymentGatewayConfigId: item.paymentGatewayConfigId };
        this.orderHttpService.deletePaymentGatewayConfig(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.queryClick();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '删除提示', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '删除提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '删除提示', detail: "调用服务失败！" });
        })
      }
    });
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.payChannelBean.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.payChannelBean.pageNum = currentPage;
    this.first = event.page * event.rows;
    //调用查询的方法
    this.query();

  }


  //重置
  resetClick() {
    this.payChannelBean = new PayChannelBean();
    this.payChannelBean.pageSize = 10;
    this.payChannelBean.pageNum = 1;
  }
  //按钮权限
	permissionCheck(id){
		return	this.commfunc.permissionCheck(id)
	  }

}
