import { Component, OnInit } from '@angular/core';
import { OrderHttpService } from './../../../http/order.http.service';
import { PayBehaveBean } from './bean/pay-behave-manage.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { specialSymbol, checkNumber } from 'app/@uisftech/common/service/validator';

//支付行为管理
@Component({
  selector: 'app-pay-behave-manage',
  templateUrl: './pay-behave-manage.component.html',
  styleUrls: ['./pay-behave-manage.component.scss'],
  providers: [Commfunc, ConfirmationService]
})
export class PayBehaveManageComponent implements OnInit {
  //表数据
  tableData: any = [];
  //bean
  payBehaveBean: PayBehaveBean = new PayBehaveBean();
  //模态框
  headerTitle: string = "修改"; //标题
  display: boolean = false; //模态框是否显示
  showModel: string;//展示哪一个
  //修改的参数
  updateValue: any = [];
  // 修改查询标志--1607bug
  updateFlag1: boolean = false;
  //分页
  total: string; //总页数
  first: number = 0;
  //提示信息
  msgs: Message[] = [];
  //校验
  userform: FormGroup;

  constructor(
    public orderHttpService: OrderHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
    this.payBehaveBean.pageParam.pageSize = 10;
    this.payBehaveBean.pageParam.pageNumber = 1;
  }

  ngOnInit() {
    this.preSet();
    //调查询
    this.query();
    //按回车键查询
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.queryClick();
      }
    }
  }
  preSet() {
    //表单校验
    this.userform = this.fb.group({
      'enumName': new FormControl('', Validators.compose([Validators.maxLength(32), specialSymbol])),
    });
  }
  //查询点击事件
  queryClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    this.payBehaveBean.pageParam.pageNumber = 1;
    this.first = 0;
    this.query();
  }
  //查询
  query() {
    this.orderHttpService.searchPaymentBehavior(this.payBehaveBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.paymentBehaviorList;
        this.total = data.totalCounts + '';
        if (!this.updateFlag1) {
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: ' 查询提示', detail: data.returnCode.message });
        }
        else {
          this.updateFlag1 = false;
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '查询提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '查询提示', detail: '调用服务失败！' });
    })
  }


  //删除按钮事件
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = { enumId: item.enumId };
        this.orderHttpService.deletePaymentBehavior(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.queryClick();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '删除提示', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '删除提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '删除提示', detail: "调用服务失败！" });
        })
      }
    });
  }
  //新增按钮事件
  addClick() {
    this.headerTitle = '新增';
    this.display = true;
    this.showModel = '1';
  }
  //添加的回调
  addCall(param) {
    this.display = param;
    this.query();
  }


  //修改按钮事件
  modifyClick(item) {
    this.headerTitle = '修改';
    this.display = true;
    this.showModel = '2';
    this.updateValue = item.enumId;
  }

  //修改的回调
  updateCall(param) {
    this.display = param.display;
    this.msgs = param.msgs;
    this.updateFlag1 = true;
    this.query();
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.payBehaveBean.pageParam.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.payBehaveBean.pageParam.pageNumber = currentPage;
    this.first = event.page * event.rows;
    //调用查询的方法
    this.query();
  }

  //重置
  resetClick() {
    this.payBehaveBean = new PayBehaveBean();
    this.payBehaveBean.pageParam.pageSize = 10;
    this.payBehaveBean.pageParam.pageNumber = 1;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}


