import { Component, OnInit } from '@angular/core';
import { OrderHttpService } from './../../../http/order.http.service'
import { ToolChannelBean } from './bean/tool-channel-relation.bean';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

//支付工具与通道关联管理
@Component({
  selector: 'app-tool-channel-relation',
  templateUrl: './tool-channel-relation.component.html',
  styleUrls: ['./tool-channel-relation.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class ToolChannelRelationComponent implements OnInit {
  //表数据
   tableData: any = [];
  //下拉框
   optionTool: any = [];  //工具下拉框
   optionChan: any = []; //通道下拉框
  //bean
   toolChannelBean: ToolChannelBean = new ToolChannelBean();
  //模态框
   headerTitle: string = "修改";//模态框的标题
   display: boolean = false;//模态框是否显示
   showModel: string;//展示哪一个
  //修改的参数
   updateValue: any = [];
  //分页
   total: string; //总页数
   first: number = 0;
  //提示信息
   msgs: Message[] = [];

  constructor(
    public orderHttpService: OrderHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
    this.toolChannelBean.pageSize = 10;
    this.toolChannelBean.pageNum = 1;
  }

  ngOnInit() {
    //调服务
    this.query();


    //支付工具下拉框
    this.orderHttpService.searchAllPaymentTool({}).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.optionTool = this.commfunc.toChangeTree(data.paymentToolList, "paymentMethodTypeName", "paymentMethodTypeId");
        this.optionTool.unshift({ label: "请选择", value: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

    //支付通道下拉框
    this.orderHttpService.findAllPaymentGatewayConfig({}).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.optionChan = data.returnList;
        this.optionChan.unshift({ label: "请选择", value: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

  }

  //查询的点击事件
  queryClick() {
    this.first = 0;
    this.toolChannelBean.pageNum = 1;
    this.query();
  }
  //查询方法
  query() {
    this.orderHttpService.findByConPaymentMethodTypeGatewayConfig(this.toolChannelBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.returnList;
        this.total = data.totalCounts;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //新增按钮事件
  addClick() {
    this.headerTitle = '新增';
    this.display = true;
    this.showModel = '1';
  }
  //添加的回调
  addCall(param) {
    this.display = param;
    this.query();
  }

  //删除按钮事件
  deleteClick(car) {
    //删除
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          paymentMethodTypeId: car.paymentMethodTypeId,//支付工具标识
          paymentGatewayConfigId: car.paymentGatewayConfigId,//支付通道标识
        };
        this.orderHttpService.deletePaymentMethodTypeGatewayConfig(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.queryClick();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      }
    });

  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.toolChannelBean.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.toolChannelBean.pageNum = currentPage;
    this.first = event.page * event.rows;
    //调用查询的方法
    this.query();
  }

  //重置
  resetClick() {
    this.toolChannelBean = new ToolChannelBean();
    this.toolChannelBean.pageSize = 10;
    this.toolChannelBean.pageNum = 1;
  }

}
