import { Component, OnInit } from '@angular/core';
import { OrderHttpService } from './../../../http/order.http.service';
import { PayRelationBean } from './bean/pay-relation-manage.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

//支付工具管理
@Component({
    selector: 'app-pay-relation',
    templateUrl: './pay-relation-manage.component.html',
    styleUrls: ['./pay-relation-manage.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class PayRelationManageComponent implements OnInit {
    //下拉框
    optionTool: any = [];//支付工具
    optionBehave: any = [];//支付行为
    optionChan: any = [];//支付通道
    //表数据
    tableData: any = [];
    // bean
    payRelationBean: PayRelationBean = new PayRelationBean();
    //模态框
    headerTitle: string = "修改";//模态框的标题  
    display: boolean = false;//模态框是否显示
    showModel: string;//展现哪一个
    //修改的参数
    updateValue: any = [];
    //分页
    total: string; //总页数
    first: number = 0;
    //提示信息
    msgs: Message[] = [];

    constructor(
        public orderHttpService: OrderHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc
    ) {
        this.payRelationBean.pageParam.pageSize = 10;
        this.payRelationBean.pageParam.pageNumber = 1;
    }

    ngOnInit() {
        //调查询服务
        this.query();

        //支付工具下拉框
        this.orderHttpService.searchAllPaymentTool({}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.optionTool = this.commfunc.toChangeTree(data.paymentToolList, "paymentMethodTypeName", "paymentMethodTypeId");
                this.optionTool.unshift({ label: "请选择", value: '' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '支付工具下拉框提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '支付工具下拉框提示', detail: '调用服务失败！' });
        })

        //支付行为下拉框
        this.orderHttpService.searchAllPaymentBehavior({}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.optionBehave = this.commfunc.toChangeTree(data.paymentBehaviorList, "enumName", "enumId");
                this.optionBehave.unshift({ label: "请选择", value: '' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '支付行为下拉框提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '支付行为下拉框提示', detail: '调用服务失败！' });
        })

        //支付通道下拉框
        this.orderHttpService.findAllPaymentGatewayConfig({}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.optionChan = data.returnList;
                // this.optionChan = this.commfunc.toChangeTree(data.returnList, "description", "paymentGatewayConfigId");
                this.optionChan.unshift({ label: "请选择", value: '' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '支付通道下拉框提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '支付通道下拉框提示', detail: '调用服务失败！' });
        })
        //按回车键查询
        let _thiss = this;
        document.onkeydown = function (event) {
            if (event && event.keyCode === 13) {
                _thiss.queryClick();
            }
        }
    }

    //查询点击事件
    queryClick() {
        this.payRelationBean.pageParam.pageNumber = 1;
        this.first = 0;
        this.query();
    }
    //查询
    query() {
        this.orderHttpService.searchPaymentMethodTypeEnumerationGateway(this.payRelationBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.paymentInfolList;
                this.total = data.totalCounts + "";
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '查询提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '查询提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '查询提示', detail: '调用服务失败！' });
        })
    }

    //新增按钮事件
    addClick() {
        this.headerTitle = '新增';
        this.display = true;
        this.showModel = '1';
    }

    //添加的回调
    addCall(param) {
        this.display = param;
        this.query();
    }

    //修改按钮事件
    modifyClick(item) {
        this.headerTitle = '修改';
        this.display = true;
        this.showModel = '2';
        this.updateValue = item;
    }

    //修改的回调
    updateCall(param) {
        this.display = param;
        this.query();
    }

    //删除按钮事件
    deleteClick(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                //条件
                let param = {
                    paymentMethodTypeId: item.paymentMethodTypeId,
                    paymentGatewayConfigId: item.paymentGatewayConfigId,
                    paymentServiceTypeEnumId: item.paymentServiceTypeEnumId,
                };
                this.orderHttpService.delPaymentMethodTypeEnumerationGateway(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.queryClick();
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '删除提示', detail: data.returnCode.message });
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '删除提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '删除提示', detail: "调用服务失败！" });
                })
            }
        });
    }

    //分页
    paginate(event) {
        //每页显示的条数
        this.payRelationBean.pageParam.pageSize = event.rows * 1;
        //当前页
        let currentPage = event.page + 1;
        this.payRelationBean.pageParam.pageNumber = currentPage;
        this.first = event.page * event.rows;
        //调用查询的方法
        this.query();
    }

    //重置
    resetClick() {
        this.payRelationBean = new PayRelationBean();
        this.payRelationBean.pageParam.pageSize = 10;
        this.payRelationBean.pageParam.pageNumber = 1;
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}

