import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { OrderHttpService } from './../../../../http/order.http.service';
import { Message, ConfirmationService } from 'primeng/primeng';
import { PayRelationBean } from './../bean/pay-relation-manage.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { checkEn } from 'app/@uisftech/common/service/validator';
// 修改
@Component({
    selector: 'pay-relation-modify',
    styleUrls: ['pay-relation-modify.component.scss'],
    templateUrl: 'pay-relation-modify.component.html',
    providers: [Commfunc, ConfirmationService]
})

export class PayRelationModify implements OnChanges {
    @Input() inValue: string;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //校验
    userform: FormGroup;
    //提示信息
    msgs: Message[] = [];
    //实体
    payRelationBean: PayRelationBean = new PayRelationBean();
    //下拉框
    optionTool: any = [];//支付工具
    optionBehave: any = [];//支付行为
    optionChan: any = [];//支付通道

    constructor(
        public fb: FormBuilder,
        public orderHttpService: OrderHttpService,
        public commfunc: Commfunc
    ) {

    }

    ngOnInit() {
        this.userform = this.fb.group({
            'paymentMethodTypeId': new FormControl('', Validators.required),
            'paymentServiceTypeEnumId': new FormControl('', Validators.required),
            'paymentGatewayConfigId': new FormControl('', Validators.required),
            'paymentService': new FormControl('', [Validators.required, checkEn]),
            'paymentServiceDescription': new FormControl('', Validators.required),
        });

        //支付工具下拉框
        this.orderHttpService.searchAllPaymentTool({}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.optionTool = this.commfunc.toChangeTree(data.paymentToolList, "paymentMethodTypeName", "paymentMethodTypeId");
                this.optionTool.unshift({ label: "请选择", value: '' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })

        //支付行为下拉框
        this.orderHttpService.searchAllPaymentBehavior({}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.optionBehave = this.commfunc.toChangeTree(data.paymentBehaviorList, "enumName", "enumId");
                this.optionBehave.unshift({ label: "请选择", value: '' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })

        //支付通道下拉框
        this.orderHttpService.findAllPaymentGatewayConfig({}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.optionChan = data.returnList;
                // this.optionChan = this.commfunc.toChangeTree(data.returnList, "description", "paymentGatewayConfigId");
                this.optionChan.unshift({ label: "请选择", value: '' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    ngOnChanges() {
        let param = {
            paymentMethodTypeId: this.inValue['paymentMethodTypeId'],
            paymentServiceTypeEnumId: this.inValue['paymentServiceTypeEnumId'],
            paymentGatewayConfigId: this.inValue['paymentGatewayConfigId']
        };
        this.orderHttpService.searchPaymentMethodTypeEnumerationGateway(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.payRelationBean = data.paymentInfolList[0];
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
        });
    }

    //提交
    modifySaveBtn() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }

        //提交服务
        this.orderHttpService.updatePaymentMethodTypeEnumerationGateway(this.payRelationBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
        });
    }

    //取消
    modifyCloseBtn() {
        this.outValue.emit(false);
    }
}